@link-color--main: #6c8a9b;
@import url( 'https://fonts.googleapis.com/css?family=Roboto|Roboto+Condensed:300,400,700&subset=cyrillic-ext');

body {
  font-family: 'Roboto', "Roboto Condensed", Arial, sans-serif;
}
h1, h2, h3, h4
.h1 , .h2, .h3, .h4 {
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
}
a {
  color: @link-color--main;
  text-decoration: underline;

  &:active,
  &:focus,
  &:hover,
  &:visited {
    text-decoration: none;
  }
}

.red {
  color: #e84c3d;
}

.italic {
  font-style: italic;
}

.bold {
  font-weight: bold;
}
