.notice {
  font-family: 'RobotoBold', 'RobotoRegular', Arial, sans-serif;
  padding: 9px 0;
  color: #32414a;

  &--main {
    background-color: #ffe600;
  }

  p {
    font-size: 12px;
    margin: 0;
  }

  a {
    color: #32414a;
    text-decoration: underline;

    &:active,
    &:focus,
    &:hover,
    &:visited {
      color: #32414a;
      text-decoration: underline;
    }
  }
}

.ico {
  &-main {
    position: relative;
    display: inline-block;
    width: 16px;
    height: 15px;
    margin-right: 7px;
    background-image: url("../img/sprite.png");
    background-repeat: no-repeat;
    top: 2px;
  }

  &-product {
    position: relative;
    display: inline-block;
    width: 16px;
    height: 15px;
    margin-right: 7px;
    background-image: url("../img/product-page-sprite.png");
    background-repeat: no-repeat;
    top: 2px;
  }
}

.form-control {
  border-radius: 0;
  border: 1px solid #d6effb;
  box-shadow: none;
  font: 16px "Roboto Condensed", RobotoRegular, Arial, sans-serif;

  &:focus {
    border-color: #ffe600;
    outline: 1px solid #ffe600;
    box-shadow: none;
  }
}

.form-horizontal .form-group {
  margin-bottom: 20px;
}

label {
  font-weight: normal;
  font: 16px "Roboto Condensed", RobotoRegular, Arial, sans-serif;
}

.dropdown-menu {
  border-radius: 0;
}

.btn {
  border-radius: 2px;
  text-decoration: none;
  transition: all .3s;

  &.btn-primary {
    background: transparent;
    color: #3598db;
    border-color: #61bdfc;

    &:hover,
    &:active,&.active,
    &:focus{
      color: #fff;
      background: #61bdfc;
      border-color: #61bdfc;
      box-shadow: none;
      outline: none;
    }
  }

  &-search {
    background: #ffe600;
    font-size: 20px;
    line-height: 20px;
    padding: 6px;
  }

  &.btn-primary--action {
    padding-top: 10px;
    padding-bottom: 10px;
    background: #ffe600;
    color: #1c2227;
    border-color: transparent;
    font-family: "Roboto Condensed", RobotoRegular, Arial, sans-serif;
    font-size: 16px;
    text-transform: uppercase;

    &:hover,
    &:active,
    &.active,
    &:focus{
      color: #1c2227;
      background: #fff066;
      border-color: transparent;
      box-shadow: none;
      outline: none;
    }
  }

  &-wx {
    width: 100%;
  }
}

.iso-dropdown {
  padding-right: 20px;
  .iso-caret::after {
    content: '\f107';
    position: absolute;
    right: 10px;
    display: inline-block;
    width: 20px;
    height: 20px;
    font-family: isolux-icon-font;
    color: #fff;
    font-size: 22px;
  }
}
.open {
  .iso-dropdown {
    .iso-caret::after {
      content: '\f106';
      color: #6c8a9b;
    }
  }
}
