.site-controls {
  &__navbar {
    height: 34px;
    min-height: 0;
    border-radius: 0;
    border: none;
    background-color: #d6effb;
    margin-bottom: 0;
    font-family: RobotoBold, RobotoRegular, Arial, sans-serif;
  }
}

.nav-controls {
  & > .nav-item > .nav-link {
    padding: 9px 8px 7px 14px;
    font-size: 12px;
    line-height: 18px;
    text-decoration: none;
    color: #32414a;

    &:active,
    &:focus,
    &:hover,
    &:visited {
      color: #32414a;
      background-color: transparent;
      text-decoration: underline;
    }
  }

  &.main-controls > .nav-item:not(:last-child) > .nav-link::after {
    content: '';
    display: block;
    height: 11px;
    width: 1px;
    background: #32414a;
    position: absolute;
    top: 12px;
    right: 0;
  }
}

.user-controls {
  .nav-item--auth,
  .nav-item--feedback {
    .nav-link {
      text-decoration: none;

      &:active,
      &:focus,
      &:hover,
      &:visited {
        text-decoration: none;
      }
    }

    >.nav-link {
      padding-left: 35px;
    }

    .bb-dotted {
      border-bottom: 1px dotted #32414a;
    }

    &:active,
    &:focus,
    &:hover,
    &:visited {}
  }

  .ico-main--auth {
    background-position: -319px -1px;
    position: absolute;
    left: 13px;
    top: 10px;
  }

  .ico-main--mobile {
    background-position: -383px -22px;
    position: absolute;
    height: 18px;
    left: 18px;
    top: 8px;
  }

  &.nav-controls > .nav-item--feedback {
    & .nav-link {
      position: relative;
      width: 218px;
      color: #fff;
      background: #6c8a9b;
      padding: 9px 8px 7px 45px;
    }

    &.open {
      box-shadow: 0 2px 6.8px 1.2px rgba(0, 0, 0, 0.15);
      > .nav-link {
        background: #ffffff;
        color: #6c8a9b;

        .ico-main--mobile {
          background-position: -383px -42px;
        }
      }
    }
  }
}

.dropdown-menu--auth {
  width: 431px;
  padding: 45px 45px 0;
  border: none;
  background: #f4fbfe;
  box-shadow: 0 2px 6.8px 1.2px rgba(0, 0, 0, 0.15);
  @media (min-width:768px) {
    .form-horizontal .control-label {
      text-align: left;
    }
  }

  .auth-btn-wrap {
    margin: 0 -45px;
    padding: 20px 45px 35px;
    background: #d6effb;
  }
}

.dropdown-menu--feedback {
  width: 218px;
  background: #6c8a9b;
  padding: 0;
  border: none;
  box-shadow: 0 2px 6.8px 1.2px rgba(0, 0, 0, 0.15);

  > .nav-item {
    > .nav-link {
      color: #fff;
      background: #6c8a9b;
      padding: 7px 20px;
      border-top: 1px solid #ccc;
      white-space: normal;
      font-size: 12px;
      line-height: 18px;

      &:hover,
      &:active,
      &:focus {
        background: #32414a;
      }
    }
  }
}

.reg-pass {
  padding-top: 20px;
  border-top: 1px solid #d6effb;
}

.nav .open {
  & > .nav-link,
  & > .nav-link:focus,
  & > .nav-link:hover {
    background: #f4fbfe;

    .bb-dotted {
      border-bottom: none;
    }
  }

  & > .nav-link.dropdown-toggle {
    z-index: 1001;
  }
}

.nav-item.nav-item--auth.open {
  z-index: 1002;
  box-shadow: 0 2px 6.8px 1.2px rgba(0, 0, 0, 0.15);
}
