.page-header {
  margin: 0;
  padding: 0;
  border: none;
}

.navbar-iso {
  font-size: 14px;

  .custom-icon {
    display: inline-block;
    margin-right: 10px;
    width: 22px;
    color: #ffe600;

    @media (min-width: 992px) and (max-width: 1199px) {
      display: none!important;
    }

    &.icon-mail {
      font-size: 12px;
    }
  }
  background-color: #1c2227;
  color: #fff;
  @media (max-width: 768px) {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    font-size: 16px;
    background-color: #fff;
    border-bottom: 1px solid #d6effb;
  }
  @media (max-width: 991px) {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    font-size: 18px;
    background-color: #fff;
    border-bottom: 1px solid #d6effb;
  }

  &-container {
    .navbar-toggle {
      float: left;
      margin: 8px 10px;
      padding: 9px 10px 4px;

      .icon-bar {
        background-color: #000;
        margin-bottom: 5px;
      }
    }

    .navbar-brand {
      width: 100px;
      height: 24.5px;
      padding: 0;
      margin-top: 14px;
    }
  }

  .open-iso {
    .navbar-1 {
      display: block;
    }

    > .navbar-2 {
      display: block;
    }

    > .navbar-3 {
      display: block;
    }
  }

  .nav-item {
    padding: 0 10px 0 0;

    > .nav-link {
      display: block;
      color: #fff;
      text-decoration: none;

      > .nav-link__content {
        position: relative;
        display: block;
        border: 1px solid transparent;
        border-bottom-color: rgba(108, 138, 155, 0.2);
        padding: 10px 25px 10px 10px;
      }
    }

    &:last-child > .nav-link .nav-link__content {
      @media (min-width: 992px) {
        border-bottom-color: transparent;
      }
    }
  }

  .submenu {
    > .nav-link .nav-link__content::after {
      content: '\f105';
      position: absolute;
      right: 10px;
      top: 4px;
      font-size: 22px;
      font-family: isolux-icon-font;
      color: #ffe600;
    }

    &.open-iso > .nav-link .nav-link__content {
      border-bottom-color: transparent;
    }

    @media (min-width: 992px) {
      border-bottom-color: transparent;
    }

    &.open-iso > .nav-link .nav-link__content::after {
      color: #62737d;
    }
  }

  &-nav {
    padding: 0;
    list-style: none;
    margin-bottom: 0;

    &.navbar-0 {
      position: relative;

      > .nav-item {
        display: inline-block;
        padding: 0;

        &--min {
          min-width: 90px;
        }
        @media (min-width: 1200px) {
          &--min {
            min-width: 120px;
          }
        }

        > .nav-link {
          display: block;
          text-decoration: none;
          text-align: center;
          color: #fff;
          border: none;

          &:hover {
            background: #ffe600;
            color: #1c2227;

            .custom-icon {
              color: #1c2227;
            }
          }
        }
      }

      > .submenu {
        &.open-iso {
          background: #ffe600;

          > .nav-link {
            color: #1c2227;
            border-bottom: none;

            .custom-icon {
              color: #1c2227;
            }
          }
        }

        > .nav-link .nav-link__content::after {
          display: none;
        }
      }
      @media (min-width: 992px) {
        display: table;

        > .nav-item {
          display: table-cell;
          vertical-align: top;
          > .nav-link {
            position: relative;
            display: block;
            text-decoration: none;
            height: 52px;
            text-align: left;
            color: #fff;
            border: none;
            padding: 5px 0 5px 5px;
            .nav-link__content {
              position: relative;
              border-bottom: none;
              padding: 0;

              &.single {
                top: 11px;
                .custom-icon {
                  top: -3px;
                }
              }

              .custom-icon {
                position: absolute;
                font-size: 20px;
                left: 5px;
                top: 8px;
                width: 20px;
                height: 20px;
                display: block;
              }
            }
          }
        }
      }

      @media (min-width: 1200px) {
        > .nav-item {
          > .nav-link .nav-link__content {
            position: relative;
            padding: 0px 0px 0px 35px;
          }
        }
      }

    }

    .navbar-1 {
      position: absolute;
      display: none;
      width: 100%;
      left: 0;
      border: 4px solid #ffe600;
      box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.3);
      left: 0;
      background: url('https://www.isolux.ru/skin/frontend/newisolux/default/images/bg/bg_2column.png') no-repeat 72% 50% #fff;

      @media (min-width: 1200px) {
        background-position: 70% 50%;
      }

      &--bg {
        background-position: 92.5% 50%;

        @media (min-width: 1200px) {
          background-position: 90.5% 50%;
        }
      }

      &__nav {
        position: relative;
        width: 33.33333333%;
        height: 100%;
        padding: 10px 0 10px 10px;
        background: #32414a;

        > .nav-item {
          &.submenu.open-iso {
            > .nav-link {
              color: #1c2227;
              border-bottom-color: transparent;
            }
          }

          > .nav-link {
            display: block;
            position: relative;
            color: #fff;
            text-decoration: none;

            &:hover {
              background: #d6effb;
              color: #1c2227;
              border-bottom-color: transparent;
            }
          }
        }

        > .submenu {
          &.open-iso {
            background: #d6effb;

            > .nav-link {
              color: #1c2227;
            }
          }

          > .nav-link:hover .nav-link__content::after {
            color: #1c2227;
          }
        }
      }
    }

    .navbar-2 {
      position: absolute;
      display: none;
      width: 200%;
      left: 100%;
      top: 0;
      bottom: 0;

      &__nav {
        width: 50%;
        min-height: 100%;
        padding: 10px 0 10px 10px;
        background: #d6effb;

        > .nav-item {
          > .nav-link {
            position: relative;
            display: block;
            text-decoration: none;
            color: #32414a;

            &:hover {
              background: #fff;
              color: #6c8a9b;
              border-bottom-color: transparent;
            }
          }
        }

        > .submenu {
          &.open-iso {
            background: #fff;

            > .nav-link {
              color: #6c8a9b;
              border-bottom-color: transparent;
              background-color: #fff;
            }
          }

          > .nav-link .nav-link__content::after {
            color: #62737d;
          }

          > .nav-link:hover .nav-link__content::after {}
        }
      }
    }

    .navbar-3 {
      position: absolute;
      display: none;
      width: 50%;
      left: 50%;
      top: 0;
      bottom: 0;

      &__nav {
        width: 100%;
        min-height: 100%;
        padding: 10px 0 10px 10px;
        background-color: #fff;

        > .nav-item {
          > .nav-link {
            display: block;
            text-decoration: none;
            color: #1c2227;

            &:hover {
              color: #6c8a9b;
            }
          }
        }
      }
    }
    @media (max-width: 991px) {
      .nav-link .nav-link__content::after {
        content: '\f105';
        position: absolute;
        right: 14px;
        top: 6px;
        font-size: 22px;
        font-family: isolux-icon-font;
        color: #62737d;
      }

      .submenu {
        > .nav-link > .nav-link__content::after {
          content: '\f107';
          color: #62737d;
          right: 12px;
          top: 6px;
        }

        &.open-iso > .nav-link > .nav-link__content::after {
          content: '\f106';
          color: #62737d;
        }
      }

      &.navbar-0 {
        padding: 6px;

        > .nav-item {
          display: block;

          > .nav-link {
            display: block;
            color: #fff;
            text-align: left;

            > .nav-link__content::after {
              display: inline-block;
            }

            :hover .nav-link__content::after {
              color: #62737d;
            }
          }
        }
      }

      .navbar-1 {
        position: static;
        height: auto!important;
        background: none;
        box-shadow: none;
        background-color: #32414a;

        &__nav {
          position: static;
          width: auto;
          height: auto;
          padding: 0;

          > .nav-item {
            padding: 0;

            > .nav-link {
              display: block;
              padding: 0 10px;

              > .nav-link__content::after {
                color: #62737d;
                font-weight: normal;
              }

            }
          }

          .submenu.open-iso {
            > .nav-link {
              font-weight: 700;
            }
          }
        }
      }

      .navbar-2 {
        position: static;
        width: auto;
        background-color: #F5F5F5;

        &__nav {
          position: static;
          width: auto;
          height: auto;
          padding: 0;
          background-color: #F5F5F5;

          > .nav-item {
            padding: 0;

            > .nav-link {
              display: block;
              padding: 0 10px;

              > .nav-link__content::after {
                color: #62737d;
                font-weight: normal;
              }

              &:hover {
                font-weight: 700;
              }
            }
          }

          .submenu.open-iso {
            > .nav-link {
              color: #1c2227;
            }
          }
        }
      }

      .navbar-3 {
        position: static;
        width: auto;
        background-color: #fff;

        &__nav {
          position: static;
          width: auto;
          height: auto;
          padding: 0;
          background-color: #fff;

          > .nav-item {
            padding: 0;

            > .nav-link {
              display: block;
              padding: 0 10px;

              > .nav-link__content::after {
                color: #62737d;
              }
            }
          }

          .submenu.open-iso {
            > .nav-link {}
          }
        }
      }
    }
  }
}

.is-collapsing {
  position: relative;
  height: 0!important;
  overflow: hidden;
  -webkit-transition-timing-function: ease;
  -o-transition-timing-function: ease;
  transition-timing-function: ease;
  -webkit-transition-duration: 0.35s;
  -o-transition-duration: 0.35s;
  transition-duration: 0.35s;
  -webkit-transition-property: height, visibility;
  -o-transition-property: height, visibility;
  transition-property: height, visibility;
}
@media (max-width: 991px) {
  .navbar-header {
    float: none;
  }

  .navbar-left,
  .navbar-right {
    float: none !important;
  }

  .navbar-toggle {
    display: block;
  }

  .navbar-collapse {
    border-top: 1px solid transparent;
    box-shadow: inset 0 1px 0 rgba(255,255,255,0.1);
  }

  .navbar-fixed-top {
    top: 0;
    border-width: 0 0 1px;
  }

  .navbar-collapse.collapse {
    display: none!important;
  }

  .navbar-nav {
    float: none!important;
    margin-top: 7.5px;
  }

  .navbar-nav > li {
    float: none;
  }

  .navbar-nav > li > a {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .collapse.in {
    display: block !important;
  }
}

.mob-nav {
  float: right;
  margin-right: 15px;
  margin-top: 15px;

  .mob-item {
    display: inline-block;
    width: 20px;
    height: 20px;
    margin-left: 20px;
    font-size: 19px;
    color: #1c2227;
    text-decoration: none;
  }
}

.side-collapse {
  background: #1c2227;
}
@media (max-width: 991px) {
  .side-collapse {
    position: fixed;
    display: none;
    height: 0;
    top: -100%;
    left: 0;
    padding-top: 46px!important;
    overflow: hidden;
    transition: height 1s, top 1s;
    background: #1c2227;
    font-family: Roboto Condensed;
    font-weight: 100;

    &__header {
      height: 52px;
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      background-color: #1c2227;
      z-index: 1050;

      .navbar-toggle {
        float: right;
        padding: 9px 9px 4px 9px;
        border-radius: 2px;
        margin-right: 20px;
        border: 1px solid #ffe600;

        .icon-bar {
          background-color: #ffe600;
        }

        &.navbar-toggle-close {
          .icon-bar:first-child {
            transform: translateY(7px) rotate(45deg);
          }
          .icon-bar:nth-child(2) {
            background: transparent;
          }
          .icon-bar:last-child {
            transform: translateY(-7px) rotate(-45deg);
          }
        }
      }
    }
  }

  .side-collapse.in {
    display: block;
    height: 100%;
    width: 100%;
    top: 0;
  }

  .navbar-collapse {
    z-index: 1040;
  }

  .overflow-hidden {
    overflow: hidden;
  }
}

.menu-title {
  font-size: 18px;
  font-weight: 400;
  text-transform: uppercase;
  color: #6c7d87;
  display: block;
  margin-top: 21px;
  padding-left: 6px;
}
