.navbar-main {
  margin: 0;
  padding: 15px 0;
  background: #fff;

  &__container {
    display: table;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }

  &__item {
    display: table-cell;
    padding: 0 10px;
    vertical-align: middle;

    &--brand {
      width: 200px;
    }

    &--search {
      width: 350px;
      padding: 0 20px;

      ::-webkit-input-placeholder {
        font-style: italic;
      }

      ::-moz-placeholder {
        font-style: italic;
      }

      :-ms-input-placeholder {
        font-style: italic;
      }

      :-moz-placeholder {
        font-style: italic;
      }
    }

    &--phone {
      width: 250px;
    }

    &--payment {
      text-align: right;

      .btn-payment {
        height: 46px;
        padding: 6px 11px 6px 44px;
        position: relative;
        border-radius: 3px;
        font-size: 16px;
        line-height: 16px;
        text-align: left;
        @media (max-width: 1199px) and (min-width: 992px) {
          padding: 6px 23px;
        }

        &__text {
          &--pay {
            display: inline-block;
            padding-left: 11px;
            border-left: 1px solid #2ab365;
          }

          &--cart {
            display: inline-block;
            padding: 9px 0 9px 11px;
            border-left: 1px solid #ddc906;
          }
        }
      }
    }
  }

  .btn-pay {
    background: #2dcc70;
    color: #fff;
    margin-right: 20px;

    &:hover {
      background: #48d286;
    }
  }

  .btn-cart {
    background: #ffe600;
    color: #1c2227;

    &:hover {
      background: #fff066;
    }
  }

  .ico-main--pay {
    background: url("../img/sprite.png") -1px -150px no-repeat transparent;
    width: 20px;
    height: 20px;
    position: absolute;
    left: 13px;
    top: 13px;
  }

  .ico-main--cart {
    background: url("../img/sprite.png") -1px -190px no-repeat transparent;
    width: 20px;
    height: 20px;
    position: absolute;
    left: 13px;
    top: 13px;
  }
}
